@use 'base/variables' as v;
@use 'base/mixins' as m;

.services{
    background-image: url('../img/wave4.svg');
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    // background-color: v.$verde;
    width: 95%;
    border-radius: 2rem;
    margin: 0 auto;
    -webkit-box-shadow: 0px 0px 46px -30px rgba(43,45,66,1);
    -moz-box-shadow: 0px 0px 46px -30px rgba(43,45,66,1);
    box-shadow: 0px 0px 46px -30px rgba(43,45,66,1);
    transition: all .9s ease-in;
    @include m.tablet {
        background-image: url(('../img/waves3.svg'));
        height: auto;
    }
    .contenedor{
        @include m.grid(2, .5rem);
        justify-content: center;
        @include m.tablet {
            grid-template-columns: repeat(4, 1fr);
        }
    }

    .services_title{
        color: v.$blanco;
        padding: 2rem;
        margin: 0;
    }

    .services-img{
        padding: 1rem;
        max-width: 95%;
        height: auto;
        margin: 0 auto;
        grid-column: 1/4;
        grid-row: 1/2 ;
        @include m.tablet {
            grid-column: 3/5;
            grid-row: 1/3;
            height: 95%;
        }
    }
}

.services-element{
    height: auto;
    padding: 1rem 2rem;
    text-align: center;
    &:nth-child(3),
    &:nth-child(4){
        grid-row: 3/4;
    }
    @include m.tablet {
        margin-top: 4rem;
        &:nth-child(2){
            grid-column: 1/2;
        }
        &:nth-child(3),
        &:nth-child(4){
            grid-row: 2/3;
            margin-top: 0;
        }
    }

    .element-title{
        font-size: 1.8rem;
        font-family: v.$openSans;
        color: v.$verde;
        text-transform: uppercase;
        @include m.tablet {
            font-size: 2rem;
        }
        span{
            color: v.$blanco;
        }
    }
    
    p{
        font-size: 1.6rem;
        font-family: v.$openSans;
        color: v.$blanco;
        padding: 1rem;
    }
}