@use 'base/variables' as v;
@use 'base/mixins' as m;

.contacto-aside{
    @include m.tablet {
        @include m.grid(3, 1rem);
    
        .lateral{
                grid-column: 1/2;
                @include m.grid(1, 0);
                justify-content: center;
                align-items: center;
                .lateral-title{
                    margin: 0;
                    margin-top: 1rem;
                }
                .client-list{
                    @include m.grid(2, 1rem);
                }   
        }
        .contacto{
            @include m.tablet{
                grid-column: 2/4;
            }
        }
    }
}