@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer{
    background-color: darken(v.$blanco, 3%);
    max-width: 100%;
    margin-top: 2rem;

    .footer-content{
        display: flex;
        justify-content: space-between;
        align-items: center;
        @include m.tablet {
            width: 80%;
        }

        h2{
            text-transform: capitalize;
            padding: 3rem;
        }

        .brands{
            font-size: 3rem;
            color: v.$verde;
            margin-right: 1rem;
        }

    }
    .copyright{
        text-align: center;
        font-size: 1.6rem;
        color: v.$gris;
        padding-bottom: 2rem;
        font-family: v.$openSans;

        span{
            color: darken(v.$verde, 10%);
        }
    }
}