@use 'base/variables' as v;
@use 'base/mixins' as m;

.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 2rem;
    width: 95%;
    @include m.tablet {
        padding: 0 1rem;
        width: 98%;
        .burgerBtn{
            display: none;
        }
    }
}

.nav{
    display: none;
    @include m.tablet {
        display: flex;
        margin-top: 0;
        gap: 1.2rem;
    }
}

.nav-link{
    font-size: 1.6rem;
    color: v.$negro;
    font-family: v.$openSans;
    font-weight: bold;
    text-transform: uppercase;
    display: block;
    padding: 1.6rem;

    &:hover{
        color: v.$blanco;
        background-color: v.$verde;
        border-radius: 1rem;
    }
}

// Menu Mobile
.burgerBtn{
    position: relative;
    display: block;
    width: 3.5rem;
    cursor: pointer;
    appearance: none;
    background: none;
    outline: none;
    border: none;
    &.burgerBtn .bar, 
    &.burgerBtn:after, 
    &.burgerBtn:before{
        content: '';
        display: block;
        width: 100%;
        border-radius: 2rem;
        height: .5rem;
        background-color: v.$negro;
        margin: 6px 0px;
        transition: .4s;
    }
    &.is-active:before{
        transform: rotate(-45deg) translate(-9px, 6px);
    }
    &.is-active:after{
        transform: rotate(45deg) translate(-9px, -7px);
    }
    &.is-active .bar{
        opacity: 0;
    }
}


.mobile-nav{
    @include m.tablet {
        display: none;
    }
    position: fixed;
    top: 0;
    left: 100%;
    width: 75%;
    min-height: 100vh;
    display: block;
    z-index: 98;
    background-color: darken(v.$blanco, 3%);
    margin-top: 10rem;
    transition: .4s;
    &.is-active{
        left: 25%;
    }
    a{
        display: block;
        width: 90%;
        text-align: center;
        margin: 0 auto 1.6rem auto;
        padding: 1.2rem 1.6rem;

        &:first-child{
            margin-top: 6rem;
        }
    }

}