/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  background-color: #F3F3F3;
  margin: 0;
}
body.body-scroll {
  padding-top: 8.6rem;
}

.contenedor {
  width: 95%;
  max-width: 180rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3, p {
  margin: 0;
}

h1 {
  font-size: 1.8rem;
}

h2 {
  font-size: 3rem;
  font-family: "Open Sans", sans-serif;
  font-weight: bold;
  text-align: center;
  text-transform: uppercase;
  color: #2B2D42;
}

h3 {
  font-size: 1.4rem;
}

p {
  font-size: 1.2rem;
}

