.header {
  height: auto;
  padding: 0 2rem;
  background-color: #F3F3F3;
}
@media (min-width: 768px) {
  .header {
    padding: 0;
  }
}
.header.fixed {
  top: 0;
  left: 0;
  position: fixed;
  box-shadow: 0.1rem 0.2rem 0.3rem #333;
  margin: 0;
  width: 100%;
}
.header h1 {
  font-size: 4rem;
  font-family: "Open Sans", sans-serif;
  font-weight: bold;
  color: #2B2D42;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 2rem;
  width: 95%;
}
@media (min-width: 768px) {
  .header {
    padding: 0 1rem;
    width: 98%;
  }
  .header .burgerBtn {
    display: none;
  }
}

.nav {
  display: none;
}
@media (min-width: 768px) {
  .nav {
    display: flex;
    margin-top: 0;
    gap: 1.2rem;
  }
}

.nav-link {
  font-size: 1.6rem;
  color: #2B2D42;
  font-family: "Open Sans", sans-serif;
  font-weight: bold;
  text-transform: uppercase;
  display: block;
  padding: 1.6rem;
}
.nav-link:hover {
  color: #F3F3F3;
  background-color: #41E2BA;
  border-radius: 1rem;
}

.burgerBtn {
  position: relative;
  display: block;
  width: 3.5rem;
  cursor: pointer;
  appearance: none;
  background: none;
  outline: none;
  border: none;
}
.burgerBtn.burgerBtn .bar, .burgerBtn.burgerBtn:after, .burgerBtn.burgerBtn:before {
  content: "";
  display: block;
  width: 100%;
  border-radius: 2rem;
  height: 0.5rem;
  background-color: #2B2D42;
  margin: 6px 0px;
  transition: 0.4s;
}
.burgerBtn.is-active:before {
  transform: rotate(-45deg) translate(-9px, 6px);
}
.burgerBtn.is-active:after {
  transform: rotate(45deg) translate(-9px, -7px);
}
.burgerBtn.is-active .bar {
  opacity: 0;
}

.mobile-nav {
  position: fixed;
  top: 0;
  left: 100%;
  width: 75%;
  min-height: 100vh;
  display: block;
  z-index: 98;
  background-color: #ebebeb;
  margin-top: 10rem;
  transition: 0.4s;
}
@media (min-width: 768px) {
  .mobile-nav {
    display: none;
  }
}
.mobile-nav.is-active {
  left: 25%;
}
.mobile-nav a {
  display: block;
  width: 90%;
  text-align: center;
  margin: 0 auto 1.6rem auto;
  padding: 1.2rem 1.6rem;
}
.mobile-nav a:first-child {
  margin-top: 6rem;
}

.about .contenedor {
  padding: 1rem;
  display: block;
}
@media (min-width: 768px) {
  .about .contenedor {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(4, 1fr);
  }
}
.about .contenedor .blink-space {
  background-image: url("../img/secretary.svg");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: left center;
  height: 45rem;
}
@media (min-width: 768px) {
  .about .contenedor .blink-space {
    height: 60rem;
    grid-column: 1/3;
  }
}
.about .contenedor h3 {
  color: #2B2D42;
  font-family: "PT Sans", sans-serif;
  font-size: 3rem;
  padding: 3rem;
  margin: 0;
}
.about .contenedor h3 .class {
  font-weight: bold;
  color: #41E2BA;
  font-style: normal;
}

.about-info {
  display: block;
}
@media (min-width: 768px) {
  .about-info {
    grid-column: 3/5;
    margin-top: 8rem;
  }
}
.about-info p {
  color: #2B2D42;
  font-family: "Open Sans", sans-serif;
  font-size: 1.8rem;
  padding: 3rem;
}

.services {
  background-image: url("../img/wave4.svg");
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 95%;
  border-radius: 2rem;
  margin: 0 auto;
  -webkit-box-shadow: 0px 0px 46px -30px rgb(43, 45, 66);
  -moz-box-shadow: 0px 0px 46px -30px rgb(43, 45, 66);
  box-shadow: 0px 0px 46px -30px rgb(43, 45, 66);
  transition: all 0.9s ease-in;
}
@media (min-width: 768px) {
  .services {
    background-image: url("../img/waves3.svg");
    height: auto;
  }
}
.services .contenedor {
  display: grid;
  gap: 0.5rem;
  grid-template-columns: repeat(2, 1fr);
  justify-content: center;
}
@media (min-width: 768px) {
  .services .contenedor {
    grid-template-columns: repeat(4, 1fr);
  }
}
.services .services_title {
  color: #F3F3F3;
  padding: 2rem;
  margin: 0;
}
.services .services-img {
  padding: 1rem;
  max-width: 95%;
  height: auto;
  margin: 0 auto;
  grid-column: 1/4;
  grid-row: 1/2;
}
@media (min-width: 768px) {
  .services .services-img {
    grid-column: 3/5;
    grid-row: 1/3;
    height: 95%;
  }
}

.services-element {
  height: auto;
  padding: 1rem 2rem;
  text-align: center;
}
.services-element:nth-child(3), .services-element:nth-child(4) {
  grid-row: 3/4;
}
@media (min-width: 768px) {
  .services-element {
    margin-top: 4rem;
  }
  .services-element:nth-child(2) {
    grid-column: 1/2;
  }
  .services-element:nth-child(3), .services-element:nth-child(4) {
    grid-row: 2/3;
    margin-top: 0;
  }
}
.services-element .element-title {
  font-size: 1.8rem;
  font-family: "Open Sans", sans-serif;
  color: #41E2BA;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .services-element .element-title {
    font-size: 2rem;
  }
}
.services-element .element-title span {
  color: #F3F3F3;
}
.services-element p {
  font-size: 1.6rem;
  font-family: "Open Sans", sans-serif;
  color: #F3F3F3;
  padding: 1rem;
}

.lateral {
  background-color: #e4e4e4;
  border-radius: 2rem;
  margin-top: 2rem;
  -webkit-box-shadow: 0px 0px 46px -30px rgb(43, 45, 66);
  -moz-box-shadow: 0px 0px 46px -30px rgb(43, 45, 66);
  box-shadow: 0px 0px 46px -30px rgb(43, 45, 66);
}
.lateral .lateral-title {
  margin: 2rem 2rem 0 2rem;
  padding: 2rem;
}

.client-list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 95%;
  margin: 0 auto;
  padding: 0;
  border-radius: 2rem;
}
.client-list .client-type {
  list-style-type: none;
  padding: 3rem;
}
.client-list .client-type:last-child {
  margin-bottom: 2rem;
}
.client-list h3 {
  font-size: 3rem;
  font-family: "PT Sans", sans-serif;
  font-weight: bold;
  text-align: center;
  color: #2B2D42;
}
.client-list h3 span {
  color: #658E9C;
}
.client-list img {
  max-width: 95%;
  height: auto;
  margin: 0 auto;
}

.contenedor h2 {
  padding: 3rem 3rem 2rem 3rem;
}

.contacto {
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  row-gap: 1rem;
  padding: 2rem;
  border-radius: 2rem;
  background-image: url("../img/contacto.svg");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  -webkit-box-shadow: 0px 0px 46px -30px rgb(43, 45, 66);
  -moz-box-shadow: 0px 0px 46px -30px rgb(43, 45, 66);
  box-shadow: 0px 0px 46px -30px rgb(43, 45, 66);
  transition: all 0.9s ease-out;
}
.contacto h2 {
  color: #F3F3F3;
  padding: 1rem;
}
.contacto .whatsapp {
  color: #6de9ca;
  margin: 0 auto;
  font-size: 8rem;
  padding: 1rem;
}
.contacto p {
  font-size: 1.8rem;
  font-family: "Open Sans", sans-serif;
  text-align: center;
  color: #F3F3F3;
}
.contacto a button {
  background-color: #41E2BA;
  padding: 1.5rem 4rem;
  color: #F3F3F3;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  text-transform: uppercase;
  font-family: "Open Sans", sans-serif;
  font-size: 1.8rem;
  margin-bottom: 0.5rem;
}
@media (min-width: 768px) {
  .contacto a button {
    width: auto;
  }
}
.contacto a button:hover {
  background-color: #20d0a4;
  cursor: pointer;
}
@media (min-width: 768px) {
  .contacto h2 {
    font-size: 4rem;
    padding-bottom: 1rem;
  }
  .contacto .whatsapp {
    font-size: 12rem;
  }
  .contacto p {
    font-size: 2.7rem;
    font-family: "Open Sans", sans-serif;
    text-align: center;
    color: #F3F3F3;
  }
  .contacto a button {
    font-size: 2.4rem;
  }
}
.contacto .contacto-mail {
  text-decoration: none;
  color: #ffe347;
}

@media (min-width: 768px) {
  .contacto-aside {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
  .contacto-aside .lateral {
    grid-column: 1/2;
    display: grid;
    gap: 0;
    grid-template-columns: repeat(1, 1fr);
    justify-content: center;
    align-items: center;
  }
  .contacto-aside .lateral .lateral-title {
    margin: 0;
    margin-top: 1rem;
  }
  .contacto-aside .lateral .client-list {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 768px) and (min-width: 768px) {
  .contacto-aside .contacto {
    grid-column: 2/4;
  }
}

.footer {
  background-color: #ebebeb;
  max-width: 100%;
  margin-top: 2rem;
}
.footer .footer-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
  .footer .footer-content {
    width: 80%;
  }
}
.footer .footer-content h2 {
  text-transform: capitalize;
  padding: 3rem;
}
.footer .footer-content .brands {
  font-size: 3rem;
  color: #41E2BA;
  margin-right: 1rem;
}
.footer .copyright {
  text-align: center;
  font-size: 1.6rem;
  color: #658E9C;
  padding-bottom: 2rem;
  font-family: "Open Sans", sans-serif;
}
.footer .copyright span {
  color: #20d0a4;
}