@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor h2{
    padding: 3rem 3rem 2rem 3rem;
}

.contacto{
    margin-top: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    row-gap: 1rem;
    padding: 2rem;
    border-radius: 2rem;
    // background-color: darken(v.$gris, 1%);
    background-image: url('../img/contacto.svg');
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    -webkit-box-shadow: 0px 0px 46px -30px rgba(43,45,66,1);
    -moz-box-shadow: 0px 0px 46px -30px rgba(43,45,66,1);
    box-shadow: 0px 0px 46px -30px rgba(43,45,66,1);
    transition: all .9s ease-out;

    h2{
        color: v.$blanco;
        padding: 1rem;
    }
    .whatsapp{
        color: lighten(v.$verde, 10%);
        margin: 0 auto;
        font-size: 8rem;
        padding: 1rem;
    }
    p{
        font-size: 1.8rem;
        font-family: v.$openSans;
        text-align: center;
        color: v.$blanco;
    }
    
    a button{
        @include m.boton(v.$verde, v.$blanco);
        text-transform: uppercase;
        font-family: v.$openSans;
        font-size: 1.8rem;
        margin-bottom: .5rem;
    }
    @include m.tablet {
        h2{
            font-size: 4rem;
            padding-bottom: 1rem;
        }
        .whatsapp{
            font-size: 12rem;
        }
        p{
            font-size: 2.7rem;
            font-family: v.$openSans;
            text-align: center;
            color: v.$blanco;
        }
    
        a button{
            font-size: 2.4rem;
        }
    }
    
    .contacto-mail{
        text-decoration: none;
        color: v.$amarillo;
    }

}