@use 'base/variables' as v;

html{
    font-size: 62.5%;
    box-sizing: border-box;
}
*,
*:before,
*:after{
    box-sizing: inherit;
}
body{
    background-color: v.$blanco;
    margin: 0;
    &.body-scroll{
        padding-top: 8.6rem; // Evita el salto al momento de fijarse la barra de navegacion
    }
}
.contenedor{
    width: 95%;
    max-width: 180rem;
    margin: 0 auto;
}

a{
    text-decoration: none;
}
h1, h2, h3, p{
    margin: 0;
}
h1{
    font-size: 1.8rem;
}
h2{
    font-size: 3rem;
    font-family: v.$openSans;
    font-weight: bold;
    text-align: center;
    text-transform: uppercase;
    color: v.$negro;
}
h3{
    font-size: 1.4rem;
}
p{
    font-size: 1.2rem;
}
