@use 'base/variables' as v;
@use 'base/mixins' as m;

.lateral{
    background-color: darken(v.$blanco, 6%);
    border-radius: 2rem;
    margin-top: 2rem;
    -webkit-box-shadow: 0px 0px 46px -30px rgba(43,45,66,1);
    -moz-box-shadow: 0px 0px 46px -30px rgba(43,45,66,1);
    box-shadow: 0px 0px 46px -30px rgba(43,45,66,1);

    .lateral-title{
        margin: 2rem 2rem 0 2rem;
        padding: 2rem;
    }
}

.client-list{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 95%;
    margin: 0 auto;
    padding: 0;
    border-radius: 2rem;
    
    .client-type{
        list-style-type: none;
        padding: 3rem;

        &:last-child{
            margin-bottom: 2rem;
        }
    }

    h3{
        font-size: 3rem;
        font-family: v.$ptSans;
        font-weight: bold;
        text-align: center;
        color: v.$negro;

        span{
            color: v.$gris;
        }
    }
    
    img{
        max-width: 95%;
        height: auto;
        margin: 0 auto;
    }
}
