@use 'base/variables' as v;
@use 'base/mixins' as m;

.about .contenedor{
    padding: 1rem;
    display: block;
    
    @include m.tablet {
        @include m.grid(4, 0);
    }

    .blink-space{
        background-image: url('../img/secretary.svg');
        background-size: contain;
        background-repeat: no-repeat;
        background-position: left center;
        height: 45rem;
        @include m.tablet {
            height: 60rem;
            grid-column: 1/3;
        }
    }

    h3{
        color: v.$negro;
        font-family: v.$ptSans;
        font-size: 3rem;
        padding: 3rem;
        margin: 0;

        .class{
            font-weight: bold;
            color: v.$verde;
            font-style: normal;
        }
    }
}

.about-info{
    display: block;
    @include m.tablet {
        grid-column: 3/5;
        margin-top: 8rem;
    }
    p{
        color: v.$negro;
        font-family: v.$openSans;
        font-size: 1.8rem;
        padding: 3rem;
    }
}