@use 'base/variables' as v;
@use 'base/mixins' as m;

.header{
    height: auto;
    padding: 0 2rem;
    background-color: v.$blanco;
    @include m.tablet {
        padding: 0;
    }

    &.fixed{
        top: 0;
        left: 0;
        position: fixed;
        box-shadow: .1rem .2rem .3rem #333;
        margin: 0;
        width: 100%;
    }

    h1{
        font-size: 4rem;
        font-family: v.$openSans;
        font-weight: bold;
        color: v.$negro;
    }
}